import ColorPickerWidget from './customWidgets/ColorPickerWidget'
import DatePickerWidget from './customWidgets/DatePickerWidget'
import PasswordWidget from './customWidgets/PasswordWidget'
export default {
  name: 'Widgets',
  schema: {
    description: 'uiSchema',
    title: 'widgets',
    type: 'object',
    properties: {
      color: {
        type: 'string',
        title: '',
        default: '#FFFFFF'
      },
      dateTime: {
        type: 'string',
        title: '',
        default: ''
      },
      password: {
        type: 'string',
        title: ''
      }
    }
  },
  uiSchema: {
    properties: {
      color: {
        widget: ColorPickerWidget
      },
      dateTime: {
        widget: DatePickerWidget
      },
      password: {
        widget: PasswordWidget
      }
    }
  },
  default: {
    color: '#409EFF',
    dateTime: '',
    password: '123456'
  }
}
